/* COLORS */
body[dark-theme] {


    --scroll-bar-color: #fff;
    /* background */
    --background-color: #232323;
    --second-background-color: #151515;
    --third-background-color: #ffffff;
    --fourth-background-color: #fff;

    /* nav */
    --navbar-desktop-color: #ffffff;
    --navbar-active-background: #383838;
    --border-header: #ffffff;
    --menu-btn-color: #ffffff;
    --menu-btn-active: #ffffff;

    /* home */
    --home-font-color: #fff;

    /* projects */
    --overlay-content-color: #383838;

    /* scroll */
    --scrolltop-background-color: #b3b3b3;
    --scrolltop-color: #383838;

    /* font color */
    --primary-font-color: #ffff;
    --secondary-font-color: #383838;

    --title-primary-color: #383838;
    --title-secondary-color: #ffffff;

    /* shadow */
    --home-shadow-color: #ddd;

    /* btn */ 
    --btn-primary-color: #383838;
    --btn-secondary-color: #ffffff;
    --btn-primary-hover: #fff;
    --btn-slider-color: #fff;

    /* overlay */
    --overlay-background: #ddd;
    --overlay-bottom: #fff;
    --overlay-icon: #383838;

    /* validation */
    --message-color: #fff;
    --error-color: #F26464;
    --success-color: #5FCD69;

    /* card */
    --card-background-color: #fff;
    --card-font-color: #383838;
    --card-border-color: #b3b3b3;

    --logo: url(../../static/media/logo-white.4fc2fdcd.svg);
    /* --lines-background: url('../assets/lines-background-dark.png'); */
}
:root {

    /* =========== Cores ========  */
    --scroll-bar-color: #eac1a0;
    /* background */
    --background-color: #FAF0E8;
    --second-background-color: #EBD5C6;
    --third-background-color: #383838;
    --fourth-background-color: #ffffff;
    --fourth-background-color: #ffffff;
    --fifth-background-color: #353535;
    --contact-background-color: #383838;

    /* nav */
    --navbar-mobile-background: #383838;
    --navbar-mobile-color: #ffffff;
    --navbar-desktop-color: #383838;
    --navbar-active-background: #ffffff;
    --border-header: #00000080;
    --menu-btn-color: #383838;
    --menu-btn-active: #fff;

    /* home */
    --home-font-color: #383838;

    /* services */
    --activity-hover: #383838;

    /* projects */
    --overlay-content-color: #fff;


    /* scroll */
    --scrolltop-background-color: #000000;
    --scrolltop-color: #ffffff;

    /* font color */
    --primary-font-color: #ffffff;
    --secondary-font-color: #383838;

    --title-font-color: #383838;
    --title-secondary-color: #383838;

    /* shadow */
    --home-shadow-color: #383838;
    --primary-shadow-color: #383838;
    --secondary-shadow-color: #fff;

    /* btn */
    --btn-primary-color: #383838;
    --btn-secondary-color: #ffffff;
    --btn-primary-hover: #000000;
    --btn-slider-color: #383838;

    /* overlay */
    --overlay-background: #383838;
    --overlay-bottom: #000000;
    --overlay-icon: #fff;

    /* validation */
    --message-color: #fff;
    --error-color: #F26464;
    --success-color: #5FCD69;

    /* card */
    --card-background-color: #383838;
    --card-font-color: #ffffff;
    --card-border-color: #fff;

    /* ========== Fontes ============ */
    --body-font: 'Poppins', sans-serif;
    --subtitle-font: 'MuseoModerno', cursive;
    --icon-font-size: 3rem;
    --biggest-font-size: 2rem;
    --h1-font-size: 5rem;
    --h2-font-size: 1.75rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: .938rem;
    --small-font-size: .813rem;
    --btn-font-size: 1.2rem;

    /*========== Font weight ==========*/
    --font-semi-bold: 600;
    --font-bold: 700;
    --font-black: 900;

    /*========== Margins ==========*/
    --mb-03: .3rem;
    --mb-1: .5rem;
    --mb-2: 1rem;
    --mb-3: 1.5rem;
    --mb-4: 2rem;
    --mb-5: 2.5rem;
    --mb-6: 3rem;
    --mb-7: 4rem;
    /*========== z index ==========*/
    --z-behind: -1;
    --z-tooltip: 10;
    --z-main: 50;
    --z-fixed: 100;
    /* Assets */
    --logo: url(../../static/media/logo.a0e8cbee.svg);
    --lines-background: url(../../static/media/lines-background-mobile.7ad804e4.png);
}

:target {
    scroll-margin-top: calc(3rem + .3rem);
    scroll-margin-top: calc(var(--mb-6) + var(--mb-03));
}

@media screen and (min-width: 968px) {
    :root {
        --biggest-font-size: 3rem;
        --h2-font-size: 1.75rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: .875rem;
    }
}

html {
    scroll-behavior: smooth;
  }

body {
    font-family: 'Poppins', sans-serif;
    font-family: var(--body-font);
    font-size: .938rem;
    font-size: var(--normal-font-size);
    background-color: #FAF0E8;
    background-color: var(--background-color);
    color: #383838;
    color: var(--secondary-font-color);
    line-height: 1.6;
}

body::-webkit-scrollbar {
    width: .6rem;
}
  
body::-webkit-scrollbar-track {
    background: #FAF0E8;
    background: var(--background-color);
}
  
body::-webkit-scrollbar-thumb {
    background-color: #eac1a0;
    background-color: var(--scroll-bar-color);
    border: 3px solid #eac1a0;
    border: 3px solid var(--scroll-bar-color);
    border-radius: 20px;
}

body.dark-theme::-webkit-scrollbar-thumb{
    background-color: #b3b3b3;
    border-color: #b3b3b3;
}


h1,
h2,
h3 {
    font-weight: 600;
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
}

ul {
    padding: 0;
    list-style: none;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    height: auto;
}


.container{
    width: 90%;
    max-width: 1440px;
    margin: 0 auto;
}


.section-title{
    color: var(--title-primary-color);
    font-size: 1.75rem;
    font-size: var(--h2-font-size);
    font-weight: 700;
    font-weight: var(--font-bold);
    text-align: center;
}

.equipe .section-title,
.projetos .section-title,
.galeria .section-title{
    color: #383838;
    color: var(--title-secondary-color);
}


section,
footer{
    padding: 2rem;
    padding: var(--mb-4);
    padding-left: 0;
    padding-right: 0;
}

.change-theme {
    position: absolute;
    right: calc(50% - var(--biggest-font-size));
    display: flex;
    color: var(--navbar-mobile-color);
    cursor: pointer;
    font-size: var(--icon-font-size);
    margin-left: 1.3rem;
    transition: 1.3s;
}

.change-theme:hover {
    text-decoration: none;
    transform: scale(1.2);
}

.change-theme.dark {
    transform: rotate(180deg);
    margin-right: calc(var(--mb-3) + 1.3rem);
    transition: 1.3s;
}


@media screen and (min-width: 968px) {

    .change-theme {
        position: static;
        right: calc(50% - 4.4rem);
        font-size: var(--h2-font-size);
        color: var(--navbar-desktop-color);
    }

}
/* ======== MENU ========== */

.navbar {
  background-color: var(--background-color);
  width: 100%;
  position: fixed;
  transition: all 0.3s ease;
  z-index: 99;
  font-size: var(--h2-font-size);
  padding: 0.5rem;
}

.navbar.active {
  background-color: var(--navbar-active-background);
  box-shadow: 0 2px 4px var(--border-header);
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-list {
  padding-top: 5rem;
  position: fixed;
  height: 100vh;
  width: 100%;
  right: -100%;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: left;
  background: var(--navbar-mobile-background);
  transition: all 0.3s ease;
  z-index: -1;
}

.nav-list.active {
  right: 0;
}

.nav-item {
  padding-left: 5%;
  padding-top: 1rem;
}

.nav-item svg {
    margin-left: .5rem;
}

.nav-link {
  color: var(--navbar-mobile-color);
  font-weight: 600;
  transition: 1.3s;
}

.nav-link:hover {
  text-decoration: underline;
}

.menu-btn {
  display: block;
  cursor: pointer;
  color: var(--menu-btn-color);
  font-size: var(--btn-font-size);
}

.menu-btn.active > svg {
  color: var(--menu-btn-active);
}

.nav-logo,
.menu-btn {
  display: flex;
  align-items: center;
}

.nav-logo img {
  content: var(--logo);
}

/* MEDIA QUERIES */

@media only screen and (min-width: 968px) {
  .menu-btn {
    display: none;
  }

  .nav-list {
    background-color: transparent;
    position: static;
    flex-direction: row;
    justify-content: center;
    font-size: var(--normal-font-size);
    padding: 0;
    width: auto;
    height: auto;
    align-items: center;
    z-index: var(--z-tooltip);
  }

  .nav-link {
    padding-left: 1.3rem;
    color: var(--navbar-desktop-color);
  }

  .nav-item {
    padding: 0;
  }
}

.inicio {
    background-color: var(--background-color);
    padding-top: var(--mb-7);
    position: relative;
    min-height: 504px;
}

.inicio-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-texts{
    text-align: center;
}

.home-img{
    max-width: 300px;
}


.home-img img{
    -webkit-filter: drop-shadow( 20px 20px 5px var(--home-shadow-color));
            filter: drop-shadow( 20px 20px 5px var(--home-shadow-color));
    margin-bottom: var(--mb-2);
}


.home-title img {
    content: var(--logo);
}

.home-title img{
    min-width: 15rem;
    max-width: 95%;
    margin-bottom: -1rem;
}

.home-subtitle{
    color: var(--home-font-color);
    font-family: var(--subtitle-font);
    font-size: var(--biggest-font-size);
    font-weight: var(--font-black);
    line-height: 2.5rem;
    margin-bottom: 1rem;
}

.home-description{
    color: var(--home-font-color);
    font-size: var(--normal-font-size);
    margin-bottom: var(--mb-4);
}

.home-btn{
    background-color: var(--btn-primary-color);
    color: var(--primary-font-color);
    border: none;
    outline: none;

    width: 12rem;
    padding: var(--mb-2) var(--mb-3); 
    border-radius: .3rem;
    
    font-size: var(--normal-font-size);
    text-align: center;
    font-weight: var(--font-semi-bold);
    
    cursor: pointer;
    
    position: absolute;
    bottom: -1.5rem;
    left: calc(50% - 6rem);
    transition: .3s;
}

.home-btn:hover{
    transform: scale(1.1) !important;
    box-shadow: 0px 0px 10px var(--secondary-shadow-color);
}

@media screen and (min-width: 968px){

    .inicio-content {
        flex-direction: row-reverse;
        justify-content: space-around;
    }

    .home-img{
        max-width: 45%;
        text-align: center; 
    }

    .home-texts{
        max-width: 50%;
        text-align: left;
        margin-bottom: var(--mb-3);
    }

    .home-title{
        margin: 0;
        margin-bottom: 1rem;
    }

    .home-subtitle{
        margin-bottom: 2rem;
    }

    .home-title img{
        min-width: 20rem;
    }
    

}
.sobre{
    padding-top: var(--mb-6);
    background-color: var(--fourth-background-color);
}

.sobre-container {
    display: flex;
    flex-direction: column;
}

.sobre-content {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
}

.sobre-texts > p {
    text-align: center;
    font-size: var(--normal-font-size);
    padding: var(--mb-1);
}


.sobre-texts > p strong{
    font-weight: var(--font-semi-bold);
    font-style: italic;
    text-decoration: underline;
    border-radius: var(--mb-03);
}

.sobre-image{
    max-width: 300px;
}

@media screen and (min-width: 968px){


    .sobre-content{
        flex-direction: row;
        justify-content: space-around;
    }

    .sobre-image{
        max-width: 45%;
        text-align: center;
    }

    .sobre-texts{
        max-width: 45%;
    }

    .sobre-texts > p {
        text-align: left;
    }

}
.skeleton {
    opacity: .7;
    animation: skeleton-animation 1s linear infinite alternate;
}

.skeleton-text {
    width: 100%;
    height: .5rem;
    margin-bottom: .25rem;
    border-radius: .125rem;
    display: inline-block;
    
}

.skeleton-text:last-child {
    margin-bottom: 0;
    width: 80%;
}

@keyframes skeleton-animation {
    0% {
        background-color: hsl(200, 20%, 70%);
    }

    100% {
        background-color: hsl(200, 20%, 95%);
    }
}
section.equipe {
    background-color: var(--second-background-color);

    padding-bottom: var(--mb-7);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;

    background-image: var(--lines-background);
    background-position: top;
    background-size: contain;
}

.equipe-container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.card {
    margin: 0 auto;
    margin-top: var(--mb-4);

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;

    width: 15rem;
    padding: var(--mb-5);
    border-radius: .5rem;


    background-color: var(--card-background-color);


    opacity: 1;
    transition: opacity .3s ease-in-out;
}


.card-img {
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    margin-bottom: var(--mb-2);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}



.card-container {
    margin-top: .5rem;
    color: var(--card-font-color);
}

.card-title {
    margin-bottom: var(--mb-1);
}

.social {
    font-size: var(--normal-font-size);
}


.social a {
    color: var(--card-font-color);
}

.social .text {
    margin-left: .24rem;
}

.slider {
    display: flex;
    box-sizing: border-box;
    margin: auto 0;
}

.card-middle {
    z-index: var(--z-main);
    border: 5px solid var(--card-border-color);
}

.card-middle.transition {
    animation: transition-card  .5s;
}

.card-left,
.card-right {
    opacity: .2;
    z-index: var(--z-tooltip);
    cursor: pointer;
}


.card-left {
    opacity: 0.5;
    transform: skewy(10deg);
    transform-origin: top right;
    margin-right: 1rem;
}

.card-left.transition {
    animation: transition-card-previous .5s;
}

.card-right {
    opacity: 0.5;
    transform: skewy(-10deg);
    transform-origin: top left;
    margin-left: 1rem;
}

.card-right.transition {
    animation: transition-card-next .5s;
}

[aria-current="page"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
    color: black;
}


@keyframes transition-card {

    from {
        transform: scale(.3)
    }

    to {
        transform: scale(1)
    }

}


@keyframes transition-card-previous {

    from {
        transform: skewy(10deg) scale(.3)
    }

    to {
        transform: skewy(10deg) scale(1)
    }

}

@keyframes transition-card-next {

    from {
        transform: skewy(-10deg) scale(.3)
    }

    to {
        transform: skewy(-10deg) scale(1)
    }
}

.slider-button{
    position: absolute;
    bottom: 10px;
    display: block;
    font-size: var(--h2-font-size);
    z-index: var(--z-main);
    cursor: pointer;
}

.buttons{
    color: var(--btn-slider-color);
    display: flex;
    justify-content: center;
}

 .nav-left {
    margin-right: 3rem
}

.nav-left:hover {
    transform: translate(-10px, 0);
    color: var(--btn-primary-hover);
    transition: 0.4s;
}

.nav-right {
    margin-left: 3rem
}

.nav-right:hover {
    transform: translate(10px, 0);
    color: var(--btn-primary-hover);
    transition: 0.4s;
}

.nav-right, .nav-left {
    transform: translate(0, 0);
    transition: 0.4s;
}
.servicos{
    background-color: var(--fourth-background-color);
}


.servicos-container{
    display: flex;
    flex-direction: column;
}

.activities{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.activity{
    width: 15rem;
    height: 15rem;
    color: var(--secondary-font-color);
    box-shadow: 0 2px 6px var(--primary-shadow-color);
    border-radius: .5rem;
    margin: var(--mb-3);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all .3s ease;
}

.activity:hover{
    background-color: var(--activity-hover);
    color: var(--primary-font-color);
    transform: scale(1.1);
}

.activity-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: var(--mb-3);
}

.activity-title{
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-3);
}

.activity-icon{
    font-size: var(--icon-font-size);
}


@media screen and (min-width: 750px){
    .activities{
        flex-direction: row;
        flex-wrap: wrap;
    }
}

@media screen and (min-width: 968px) {
    .activities{
        flex-direction: row;
        flex-wrap: nowrap;
    }
}
.projetos{
    background-color: var(--second-background-color);
    background-image: var(--lines-background);
}

.projetos-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.project-row {
    max-width: 17rem;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.project {
    width: 16rem;
    height: 13rem;
    background-color: var(--body-color);
    margin: var(--mb-2);
    transition: all .3s ease;
    position: relative;
    background-color: var(--fourth-background-color);
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .3s ease;
    background-color: var(--overlay-background);
    border-bottom: var(--mb-1) solid var(--overlay-bottom);
}

.overlay {
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.project:hover .overlay{
    opacity: .9;
}

.icon {
    color: var(--overlay-icon);
    font-size: 70px;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.overlay-content {
    width: 100%;
    background-color: var(--overlay-bottom);
    color: var(--overlay-content-color);
    font-size: var(--small-font-size);
    padding: 0.4rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.project-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
}


@media screen and (min-width: 680px){

    .project-row{
        max-width: 57rem;
    }

}
.galeria{
    width: 100%;
}

.galeria-container{
    display: grid;
    grid-template-columns: 1fr;
    width: 96%;
}


.embed{
    position: relative;
    width: 100%;
    padding-bottom: 56.25%;
}

.embed iframe{
    position: absolute;
    width: 100%;
    height: 100%;
    border: none;
    z-index: var(--z);
}



@media screen and (min-width: 968px){
    .galeria-container{
        max-width: 800px;
        
    }

}
